"use client"

import type { Metadata } from "next";
import localFont from "next/font/local";
import "./globals.css";
import AnimatedBackground from "@/app/components/AnimatedBackground";
import Nav from "@/app/components/Tabs/Nav";
import Providers from "./providers";
import {AnimatePresence, motion} from "framer-motion";
import {usePathname} from "next/navigation";

const dirtylineFont = localFont({
    src: [
        {
            path: "../public/fonts/Dirtyline 36daysoftype 2022.woff2",
            weight: "400",
            style: "normal",
        },
        {
            path: "../public/fonts/Dirtyline 36daysoftype 2022.woff",
            weight: "400",
            style: "normal",
        },
        {
            path: "../public/fonts/Dirtyline 36daysoftype 2022.ttf",
            weight: "400",
            style: "normal",
        },
    ],
    variable: "--font-dirtyline",
    display: "swap",
});

const nohemiFont = localFont({
    src: [
        {
            path: "../public/fonts/Nohemi-Thin.woff2",
            weight: "100",
            style: "normal",
        },
        {
            path: "../public/fonts/Nohemi-Regular.woff2",
            weight: "400",
            style: "normal",
        },
        {
            path: "../public/fonts/Nohemi-Bold.woff2",
            weight: "700",
            style: "normal",
        },
        {
            path: "../public/fonts/Nohemi-ExtraBold.woff2",
            weight: "800",
            style: "normal",
        },
    ],
    variable: "--font-nohemi",
    display: "swap",
});


export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
    const pathname = usePathname();

    return (
    <html lang="en">
    <body className={`${dirtylineFont.variable} ${nohemiFont.variable} antialiased`}>
    {/* Background for all pages */}
    <AnimatedBackground/>

    {/* Content */}

    <main>
        <div className="container mx-auto px-8">
            <AnimatePresence mode="wait"> {/* Ensures animations complete before next page loads */}
                <motion.div
                    key={pathname} // Triggers animation when route changes
                    initial={{ opacity: 0, x: 50 }}
                    animate={{ opacity: 1, x: 0 }}
                    exit={{ opacity: 0, x: -50 }}
                    transition={{ duration: 0.4, ease: "easeInOut" }}
                >
                    {children}
                </motion.div>
            </AnimatePresence>
        </div>
    </main>

    {/* Navigation menu and logo */}
    <Nav/>

    </body>
    </html>
);
}
